
import NotFound from '../NotFound/NotFound';
import XHeader from 'vux/src/components/x-header/index.vue'


export default {
name: 'Service',
components: {
XHeader,
NotFound
},
created() {
this.getData();
},
methods: {
async getData() {
let res = await this.$http('/queryCustomerServiceInfo');
console.log(res);
window.open(res.returnMap.customerServiceUrl);
}
}
};



